<template lang="">
  <v-container style="overflow:hidden; width:100%; right:10px">
    <v-list
      maxHeight="86.5vh"
      style="padding:10px;overflow-y: auto; width:100%; overflow-x:hidden"
    >
      <h2>{{ deckCount }} Card(s)</h2>
      <v-expansion-panels>
        <v-expansion-panel
          v-for="uniqueCard in deckList"
          :key="uniqueCard[0].mtgo_id"
        >
          <v-expansion-panel-header>
            [{{ uniqueCard[1] }}] -
            {{ uniqueCard[0].name }}
            <v-spacer></v-spacer>
            {{ uniqueCard[0].mana_cost }}
          </v-expansion-panel-header>

          <v-expansion-panel-content>
            <v-row>
              <v-col>
                <v-img
                  v-if="uniqueCard[0].image_uris"
                  :src="uniqueCard[0].image_uris.normal"
                  height="250px"
                  width="180px"
                  style="margin:5px"
                  align-center
                  contain
                />
              </v-col>
              <v-col>
                <v-row style="padding-top:10px">
                  <h4>{{ uniqueCard[0].name }}</h4>
                  <h5>{{ uniqueCard[0].oracle_text }}</h5>
                </v-row>
                <v-row>
                  <v-list-item-subtitle
                    v-if="uniqueCard[0].type_line.includes('Creature')"
                    >{{ uniqueCard[0].power }}/{{
                      uniqueCard[0].toughness
                    }}</v-list-item-subtitle
                  >
                  <v-list-item-subtitle
                    v-if="uniqueCard[0].type_line.includes('Planeswalker')"
                    >{{ uniqueCard[0].loyalty }}</v-list-item-subtitle
                  >
                </v-row>

                <v-row>
                  <v-btn color="secondary" @click="removeCard(uniqueCard[0])">
                    <v-icon>mdi-minus</v-icon>
                  </v-btn>
                  <v-btn color="secondary" @click="addCard(uniqueCard[0])">
                    <v-icon>mdi-plus-thick</v-icon>
                  </v-btn>
                </v-row>
              </v-col>
            </v-row>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </v-list>
  </v-container>
</template>
<script>
export default {
  name: "DeckView",
  props: ["deckList", "deckCount"],
  methods: {
    removeCard(card) {
      this.$emit("remove-card", card);
    },
    addCard(card) {
      this.$emit("add-card", card);
    }
  }
};
</script>
<style lang=""></style>
