<template lang="">
  <v-container style="overflow:hidden; width:100%; right:10px">
    <DeckView
      :deckList="deckList"
      :deckCount="deckCount"
      v-on:add-card="addCard"
      v-on:remove-card="removeCard"
    />
  </v-container>
</template>
<script>
import DeckView from "../components/DeckView.vue";

export default {
  name: "RightPage",
  components: { DeckView },
  methods: {
    removeCard(card) {
      this.$emit("remove-card", card);
    },
    addCard(card) {
      this.$emit("add-card", card);
    }
  },
  props: ["deckList", "deckCount"]
};
</script>
<style lang=""></style>
